*{
  font-family: monospace; 
     font-size: 14px;
}
.dndflow {
  flex-direction: column;
  display: flex;
  height: 100%;
}

.dndflow aside {
  border-right: 1px solid #eee;
  padding: 15px 10px;
  font-size: 12px;
  background: #fcfcfc;
}

.dndflow aside .description {
  margin-bottom: 10px;
}

.dndflow .dndnode {
  height: 28px;
  padding: 16px;
  border: 1px solid #bababa;
  margin-bottom: 10px;
  display: flex;
  cursor: grab;
  font-family: monospace;
  background-color: white;
  font-size: 14px;
  margin-top: 20px;
  margin-left: 12px;
}

.dndflow .dndnode.input {
  border-color: #0041d0;
}

.dndflow .dndnode.output {
  border-color: #ff0072;
}

.dndflow .reactflow-wrapper {
  flex-grow: 1;
  height: 100%;
}

.dndflow .circle {
  border-radius: 50%;
  width: 30%;
  height: 13%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: grab;
}

.topbar{
height: 100px;
border-bottom: 2.5px solid black;
background-color: #ececec;
}
.form{
  padding-top: 23px;
  padding-left: 18px;
}

.label{
  display: inherit;
  padding-bottom: 7px;
}

.nodetext{
  height: 30px;
  width: 230px;
}

.update{
  width: 75px;
  background-color: black;
  color: #e2e2e2;
  height: 36px;
  margin-left: 20px;
  font-size: 17px;
  border: none;
  cursor: pointer;

}

.rightside{
  float: right;
  padding-right: 45px;
}
.leftside{
  float:left;
}

.download{
  width: 95px;
  background-color: black;
  color: #e2e2e2;
  height: 36px;
  font-size: 17px;
  border: none;
  cursor: pointer;
  margin-top: 24px;
}

  .dndflow {
    flex-direction: row;
  }

  .dndflow aside {
    width: 20%;
    max-width: 250px;
    background-color: #ececec;
    border-left: 2.5px solid;
  }

  input[type="file"] {
    display: none;
}

.custom-file-upload {
  display: inline-block;
  padding: 6px 12px;

  width: 75px;
  background-color: black;
  color: #e2e2e2;
  height:23px;
  margin-left: 20px;
  font-size: 17px;
  border: none;
  cursor: pointer;
  margin-right: 20px;
  text-align: center;
}